import { Injectable }    from '@angular/core';
import { Http, Headers, Response } from '@angular/http';

import { Observable }     from 'rxjs/Observable';

import 'rxjs/add/operator/toPromise';

import { Movie } from './movie';

@Injectable()
export class MovieService {
  private moviesServiceUrl = 'http://online-movies.azurewebsites.net/api/Movies/';
  private allMoviesPageUrl = this.moviesServiceUrl + 'GetMovies/';
  private getMoviesByGenreUrl = this.moviesServiceUrl + 'GetMoviesByGenre/';
  private specificMovieUrl = this.moviesServiceUrl + 'GetMovie/';
  private getAllMoviesLastPageUrl   = this.moviesServiceUrl + 'GetAllMoviesLastPage';
  private getGenreMoviesLastPageUrl = this.moviesServiceUrl + 'GetGenreMoviesLastPage/';

  constructor(private http: Http) {

  }

  // getMovies(page: number) {
  //   return this.http.get('http://localhost:7000/search?q=chem')
  //    .map((res:Response) => res.json());

  //   //  return this.http.get(this.allMoviesPageUrl + page)
  //   //                 .map((res: Response) => res.json());
  // }

  getMovies(page: number): Promise<Movie[]> {
    return this.http.get(this.allMoviesPageUrl + page)
      .toPromise()
      .then(response => response.json() as Movie[])
      .catch(this.handleError);
  }

  getMoviesByGenre(genreId: number, page: number): Promise<Movie[]> {
    return this.http.get(this.getMoviesByGenreUrl + genreId + '/' + page)
      .toPromise()
      .then(response => response.json() as Movie[])
      .catch(this.handleError);
  }

  getMovie(id: number): Promise<Movie> {
    return this.http.get(this.specificMovieUrl + id)
      .toPromise()
      .then(response => response.json() as Movie)
      .catch(this.handleError);
  }

  getAllMoviesLastPage(): Promise<number> {
    return this.http.get(this.getAllMoviesLastPageUrl)
      .toPromise()
      .then(response => response.json() as number)
      .catch(this.handleError);
  }

  getGenreMoviesLastPage(genreId: number): Promise<number> {
    return this.http.get(this.getGenreMoviesLastPageUrl + genreId)
      .toPromise()
      .then(response => response.json() as number)
      .catch(this.handleError);
  }

  private handleError (error: Response | any) {
    // In a real world app, we might use a remote logging infrastructure
    let errMsg: string;
    if (error instanceof Response) {
      const body = error.json() || '';
      const err = body.error || JSON.stringify(body);
      errMsg = `${error.status} - ${error.statusText || ''} ${err}`;
    } else {
      errMsg = error.message ? error.message : error.toString();
    }
    console.error(errMsg);
    return Observable.throw(errMsg);
  }
}


